<template>
    <el-image class="diy-img"
              :src="src"
              :fit="fit">
        <div slot="error">
            <div class="img-error">
                <div class="error-box">
                    <p v-if="errorText===''"><img class="img" src="../../assets/img/common/load_error.png"></p>
                    <p v-if="errorText===''">{{$L('加载失败')}}</p>
                    <p v-if="errorText!==''">{{errorText}}</p>
                </div>
            </div>
        </div>
    </el-image>
</template>

<script>
    export default {
        name: "diyImage",
        props:{
            fit:{
                type:String,
                default:'cover'
            },
            src:{
                type:String,
                default:''
            },
            errorText:{
                type:String,
                default:''
            }
        }
    }
</script>

<style lang="scss" scoped>
    .diy-img {
        width: 100%;
        height: 100%;
        /deep/ .el-image__inner{
            width: inherit;
            height: inherit;
        }
        .img-error{
            width: 100%;
            height: 100%;
            background-color: #EBEEF5;
            position: absolute;
            top: 0;
            left: 0;

            .error-box{
                position: absolute;
                width: 100%;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                text-align: center;
                color: #909399;
                font-size: 12px;

                .img{
                    width: 80%;
                    border: none;
                }
            }
        }
    }
</style>