<template>
    <div class="modify">
        <div class="modify-content">
            <div class="select-item">
                <span class="p">{{ $L(' 选择页面') }}：</span>
                <el-select class="search" v-model="langType" size="small"
                           @change="changeLanguageFunc" v-if="routerData.hasOwnProperty('langList')">
                    <el-option
                            v-for="item in routerData.langList"
                            :key="item.langId"
                            :label="item.name"
                            :value="item.langId">
                    </el-option>
                </el-select>
                <el-select class="search-site" v-model="siteType" size="small"
                           @change="changeSiteFunc" v-if="routerData.hasOwnProperty('isV8') && routerData.isV8">
                    <el-option
                            v-for="item in siteList"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                    </el-option>
                </el-select>
                <el-select class="search-page" v-model="pageType" size="small"
                           @change="changePageFunc"  v-if="routerData.hasOwnProperty('pagesList')">
                    <el-option
                            v-for="item in routerData.pagesList"
                            :key="item.pageId"
                            :label="item.name"
                            :value="item.pageId">
                    </el-option>
                </el-select>
            </div>
            <div class="table-item">
                <p class="p">{{ $L('可编辑模块') }}：</p>
                <el-table
                        v-loading="loading"
                        :data="routerData.blockList"
                        :empty-text="$L('此页面暂无模块可编辑')"
                        class="table"
                        :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666',fontSize:'14px'}">
                    <el-table-column
                            prop="title"
                            :label="$L('模块标题')"
                            align="center"
                            min-width="30%">
                    </el-table-column>
                    <el-table-column
                            :label="$L('描述说明')"
                            align="left">
                        <div slot-scope="scope">
                            <template v-if="scope.row.moduleType == 'text' || scope.row.moduleType == 'html'">
                                <span v-html="scope.row.text"></span>
                            </template>
                            <template v-else>
                                <img class="img-box" v-if="scope.row.file == ''" src="../../../assets/img/common/defaultimg.jpg"/>
                                <img class="img-box" v-else :src="scope.row.file">
                            </template>
                        </div>
                    </el-table-column>
                    <el-table-column
                            :label="$L('状态')"
                            align="center"
                            width="100">
                        <div slot-scope="scope">
                            {{ scope.row.isSet == 1 ? $L('已设置') : '' }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="operate"
                            :label="$L('操作')"
                            align="center"
                            width="150">
                        <div slot-scope="scope">
                            <span class="mainFontColor" @click="editModel(scope.row)">{{ $L('编辑') }}</span>
                        </div>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    import DiyImage from "../../../components/common/diyImage";
    export default {
        name: "modify",
        components: {DiyImage},
        data (){
            return {
                loading:true,
                routerData:{},
                langType:0,
                pageType:'',
                siteType:0,
                siteList:[
                    {
                        id:0,
                        name:this.$L('电脑站')
                    },{
                        id:1,
                        name:this.$L('手机站')
                    }
                ],

            }
        },
        created (){
            this.getData();
        },
        methods: {
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    langId: _self.langType,
                    pageId: _self.pageType
                };
                if(_self.routerData.isV8){
                    arr.isMobile = _self.siteType;
                }
                _self.$api('getModuleList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.routerData = reData.data;
                        if(_self.routerData.hasOwnProperty('pageId')){
                            if(!isNaN( Number(_self.routerData.pageId))){
                                _self.pageType = Number (_self.routerData.pageId);
                            } else {
                                _self.pageType = _self.routerData.pageId;
                            }
                        }

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回我的分销'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorList'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            changeLanguageFunc(val){
                this.langType = val;
                this.siteType = 0;
                this.pageType = '';
                this.getData();
            },
            changePageFunc(val){
                this.pageType = val;
                this.getData();
            },
            changeSiteFunc(val){
                this.siteType = val;
                this.pageType = '';
                this.getData();
            },
            editModel(data){
                if(data.hasOwnProperty('idBlock')){
                    this.$router.push({'name':'distributorEditModify',query:{moduleType:data.moduleType,idBlock:data.idBlock,blockName:data.blockName,langId:this.langType,pageId:this.pageType}});
                } else {
                    this.$router.push({'name':'distributorEditModify',query:{moduleType:data.moduleType,blockName:data.blockName,langId:this.langType,pageId:this.pageType}});
                }

            }
        }
    }
</script>

<style lang="scss" scoped>
    .modify{
        position: relative;
        padding: 20px !important;
        .modify-content{
            color: #606266;
            font-size: 14px;
            padding: 20px;
            background: #fff;
            .select-item{
                .p{
                    width: 100px;
                    display: inline-block;
                }
                .search{
                    width: 120px;
                }
                .search-page{
                    width: 400px;
                    margin-left: 10px;
                }
                .search-site{
                    margin-left: 10px;
                    width: 120px;
                }
            }
            .table-item{
                padding-top: 10px;
                .p{
                    height: 30px;
                    line-height: 30px;
                    margin-bottom: 10px;
                }
                .operate{
                    cursor: pointer;
                }
                .img-box{
                    width: 120px;
                    height: 120px;
                    border: 1px solid #eee;
                }
                .mainFontColor{
                    cursor: pointer;
                }
            }
        }
    }
</style>